import Vue from "vue";
import Element from "element-ui";
import "element-ui/lib/theme-chalk/reset.css";
import "element-ui/lib/theme-chalk/index.css";
import "bootstrap/scss/bootstrap-grid.scss";
import "@fortawesome/fontawesome-free/css/all.css";
import "@/assets/main.scss";
import elementLocale from "element-ui/lib/locale";
import enLocale from "element-ui/lib/locale/lang/en";
import cnLocale from "element-ui/lib/locale/lang/zh-CN";
import twLocale from "element-ui/lib/locale/lang/zh-TW";
import viLocale from "element-ui/lib/locale/lang/vi";
import idLocale from "element-ui/lib/locale/lang/id";
import thLocale from "element-ui/lib/locale/lang/th";
import jaLocale from "element-ui/lib/locale/lang/ja";

const nowUrl = new URL(location.href);
const locale =
  nowUrl.searchParams.get("lang") || process.env.VUE_APP_I18N_LOCALE;

Vue.use(Element);

const langList = {
  "en-us": enLocale,
  "zh-cn": cnLocale,
  "zh-tw": twLocale,
  "vi-vn": viLocale,
  "id-id": idLocale,
  "th-th": thLocale,
  "ja-jp": jaLocale
};

elementLocale.use(langList[locale]);
